      searchMac(){
        seoHttp.searchMac({mac:this.input,areaCode:this.select1,
          name:this.input,
          serviceCode:this.select2,
          deviceNum:this.select3,
          page:this.page1.currentPage,
          pageSize:this.page1.size}).then((result)=>{
          this.tableData1 = result.data.features;
          this.page1.total = result.totalRecord;
          var ifr = document.getElementById("xl_search").contentWindow
          ifr.logicalFilterLayer();
        }).catch(() => {});
