      macDialogCk(mac){
        seoHttp.searchMac({mac:mac}).then((result)=>{
          this.macForm = result.data.features[0].properties;
          this.macDialog = true;
        }).catch(() => {});
        seoHttp.macChart({mac:mac}).then((result)=>{
          this.echartsData.data = [];
          this.echartsData.ydata = [];
          for (let item of result.data) {
            this.echartsData.data.push(item.appearNum)
            this.echartsData.ydata.push(item.serviceName)
          }
        }).catch(() => {});
        this.macDialogMac = mac;
        let params={mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size}
        this.netWorkLog(params);
      },
      netWorkLog(params){
//        this.pageSwjl.currentPage = 1;
        seoHttp.netWorkLog(params).then((result)=>{
          this.macTable = result.data;
          this.pageSwjl.total=result.totalRecord;
          this.pageSwj2.total = Math.ceil(result.totalRecord/this.pageSwjl.size);
        }).catch(() => {});
      },
      handleSizeChangeSwjl(val) {
        this.pageSwjl.size = val;
        this.netWorkLog({mac:this.macDialogMac,page:this.pageSwjl.currentPage,pageSize:this.pageSwjl.size});
