  .searchBoxBt2{position:absolute;right:-40px;top:50%;width:20px;height:40px;padding:20px 10px;margin-top:-40px;background:#fff;cursor:pointer;border-radius:0 5px 5px 0;color:#333;font-size:14px;line-height:20px;text-align:center;box-shadow: 2px 2px 3px 0 rgba(0,0,0,.1);}
