  .tableBoxBt{position:absolute;right:0px;top:50%;width:20px;height:40px;padding:20px 10px;margin-top:-40px;background:#fff;cursor:pointer;border-radius:5px 0 0 5px;color:#333;font-size:14px;line-height:20px;text-align:center;border-top:1px solid rgba(0,0,0,.1);box-shadow: -2px 2px 5px 0 rgba(0,0,0,.1);}
  .tableBoxClose{position:absolute;right:10px;top:10px;font-weight:bold;font-size:16px;color:#333;cursor:pointer; }
