  .tableBox{width:90%;background:#fff;position:absolute;right:0px;top:10px;border-radius:5px 0 0 5px;z-index:1;box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);padding:20px;min-height:640px;transition:width .3s;
    &.small{width:230px;}
  }
  .pageBox{text-align: right;margin-top: 15px;}
  .addButton{position:absolute;top:10px;left:10px;}
  .macChart{width:100%;height:300px;}
